.kanban {
    display:inline-flex;    
}

.section {
    background-color:#383838;
    width: 80px;
    padding:10px;
    border-radius: 10px;
    margin-left:5px;
    font-size: 1rem;
    cursor:pointer;
    text-align:center;
    flex-direction:column;
    display:inline-flex;
    
}

.control{
    background-color:#383838;
    width: 80px;
    padding:10px;
    border-radius: 10px;
    margin: 10px;
    font-size: 1rem;
    cursor:pointer;
    text-align:center;
    flex-direction:column;
    display:inline-flex;
}

.title {
    font-size: 1rem;
    margin: 10px 0 50px;
    text-align:center;
}

.content{ 
    width: 60px;
    background-color: #212121;
